
@media screen and (min-width:1367px) {
#memberCenter_form {
      /*  适配好的样式 */
      height: 36%;
      /*overflow-y: scroll; 
      overflow-x: hidden;*/
      width: 30%;
      box-shadow: 0 5px 15px rgba(0,0,0,.5);
      position: fixed;
      display: block;
      top:62%;
      left:50%;
      margin-left:-18%;
      margin-top:-25%;  
      padding: 70px 80px 0 30px;
      margin-right: 20px;
      border: 1px solid #ccc;
      overflow: hidden;
      background: #fff;
      border-radius: 2px;
}
}
@media screen and (max-width:1367px) {
#memberCenter_form {
      height: 40%;
      width: 30%;
      box-shadow: 0 5px 15px rgba(0,0,0,.5);
      position: fixed;
      display: block;
      top: 62%;
      left: 50%;
      /*overflow-y: scroll;*/
      margin-left: -22%;
      margin-top: -24%;
      padding: 80px 150px 20px 40px;
      margin-right: 20px;
      border: 1px solid #ccc;
      background: #fff;
      border-radius: 2px;
}
}
.loading_class {
    background: rgba(68,68,68,0.4);
}
.addaccount_button:nth-of-type(1) {
		background: #f87e2b;
		border: none;
}
.addaccount_button:nth-of-type(1):hover {
		background: rgba(248,126,43,0.9);
}
.addaccount_button:nth-of-type(2):hover {
		border: 1px solid rgb(248,126,43);
		color: rgb(248,126,43);
}
.addaccount_button {
		width: 120px;
		height: 50px;
}
#addaccount_title {
    position: absolute;
    left: 0px;
    top: 0px;
    padding-left: 20px;
    width: 98%;
    height: 40px;
    font-size: 18px;
    font-weight: 600;
    overflow-x: hidden;
    line-height: 40px;
    color: #444;
    border-bottom: 1px solid #eee;
}
#addaccount_title span {
		float: right;
		margin-right: 14px;
		cursor: pointer;
}
#addaccount_title span a:hover {
    color: #666;
}
.el-input__inner {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  line-height: 1;
  outline: 0;
  padding: 3px 10px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}
.el-input__inner::-webkit-input-placeholder {
  color: #ddd;
}
.el-date-table td.current:not(.disabled), .el-date-table td.end-date, .el-date-table td.start-date {
  background: black !important;
  color: #fff !important;
}
.el-input__inner:hover {
  border: 1px solid #bbb;
}
.el-textarea__inner {
  display: block;
  resize: vertical;
  padding: 5px 7px;
  line-height: 1.5;
  width: 100%;
  color: #1f2d3d;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}



