
.el-pagination{
    margin-top: 20px;
    border-left: none;
    margin-left: 0;
    padding-left: 0;
    margin-bottom: 10px;
}
#partnerManager_router {
  width: 100%;
  height: 100%;
  background: rgba(68, 68, 68, 0.6);
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
}
#partner_header {
  /*width: 100%;*/
  height: 70px;
  background: #fff;
  border: 1px solid #e7ecf1;
  /*padding: 20px 30px 20px 30px;*/
}
#partner_header .partner_content {
  background: #fff;
  overflow: hidden;
}
#partner_header .partner_my_input {
  width: 140px;
  height: 30px;
  outline: none;
  margin-top: 4px;
  border-radius: 4px;
  text-indent: 8px;
  border: 1px solid #ddd;
  display: inline-block;
}
#partner_header label:nth-of-type(1) {
  height: 70px;
  width: 200px;
  line-height: 70px;
  margin-left: 30px;
  margin-right: 20px;
  font-size: 14px;
  float: left;
}
#partner_header label:nth-of-type(1)>span {
  margin-right: 3px;
}
#partner_header label:nth-of-type(2) {
  height: 70px;
  font-size: 14px;
  width: 300px;
  line-height: 70px;
  margin-left: 20px;
  float: left;
}
#partner_header label:nth-of-type(2)>span {
  margin-right: 3px;
}
#partner_header label:nth-of-type(3) {
  height: 70px;
  width: 300px;
  font-size: 14px;
  line-height: 70px;
  margin-left: -50px;
  float: left;
}
#partner_header label:nth-of-type(3) .el-select>.el-input input {
  width: 95px;
  border-radius: 4px;
  border: 1px solid #ddd;
}
#partner_header label:nth-of-type(3) .el-input {
  width: 80px;
  text-align: center;
}
#partner_header label:nth-of-type(3) .el-input input {
  border-radius: 4px;
  border: 1px solid #ddd;
}
#partner_table {
  padding: 0 30px 10px 30px;
  background: #fff;
  border: 1px solid #e7ecf1;
  border-bottom: none;
  margin-top: 20px;
}
#partner_add {
  width: 90%;
  height: 68px;
  line-height: 68px;
}
#partner_add button {
  width: 100px;
  height: 30px;
  line-height: 30px;
  display: inline-block;
  border: none;
  outline: none;
  border-radius: 4px;
  font-size: 12px;
  color: #fff;
  background: rgba(66, 66, 66, 0.8);
  transition: all .2s linear 0s;
}
#partner_add button:hover {
  background: rgb(66, 66, 66);
  cursor: pointer;
}
#partner_page {
  padding: 4px 10px 0 20px;
  padding-bottom: 100px;
  background: #fff;
  border: 1px solid #e7ecf1;
  border-top: none;
  min-height: 230px;
}
#partner_header .my_btn {
    width: 80px;
    float: right;
    height: 36px;
    line-height: 11px;
    margin-right: 30px;
    color: #fff;
    margin-top: 17px;
    outline: none;
    border: none;
    /* border-radius: 4px; */
    background: rgba(52,52,67, 0.8);
}
#partner_header .my_btn:hover {
    background: rgba(52,52,67, 0.9);
    color: #fff;
}
.partner_button:nth-of-type(1) {
  background: #f87e2b;
  border: none;
  color: #fff;
  margin-left: 70px;
}
.partner_button:nth-of-type(1):hover {
  background: rgba(248, 126, 43, 0.9);
}
.partner_button:nth-of-type(2) {
  background: #fff;
  color: #444;
  border: 1px solid rgba(196,196,196,1);
}
.partner_button:nth-of-type(2):hover {
  border: 1px solid rgb(248, 126, 43);
  color: rgb(248, 126, 43);
}
.partner_button {
  width: 120px;
  height: 50px;
  float: left;
  margin-top: -30px;
  margin-bottom: 20px;
}
.el-input__inner {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  line-height: 1;
  outline: 0;
  padding: 3px 10px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}
.el-date-table td.current:not(.disabled), .el-date-table td.end-date, .el-date-table td.start-date {
  background: black !important;
  color: #fff !important;
}
.el-input__inner:hover {
  border: 1px solid #bbb;
} 



