
div.theme {
  padding: 30px 0 0 20px;
}
.my_icon_class {
  margin-right: 5px;
  font-size: 18px;
}
.el-menu {
  border-radius: 2px;
  list-style: none;
  position: relative;
  margin: 0;
  padding-left: 0;
  background-color: #fff;
}
.el-submenu .el-menu {
  background: #fff;
}
div.siderBar {
  height: 945px;
  background: #fff;
}
.el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item.is-active,
.el-menu-item.is-active {
  color: #fff;
  background: #cecece;
  border-left: 3px solid #fced4a;
}
.el-submenu .el-menu-item {
  height: 40px;
  line-height: 40px;
}
.el-menu-item, .el-submenu__title {
    height: 45px;
    line-height: 45px;
    font-size: 14px;
    color: #999999;
    padding: 0 20px;
    cursor: pointer;
    position: relative;
    /*transition: border-color .3s,background-color .3s,color .3s;*/
    box-sizing: border-box;
    white-space: nowrap;
}
.el-menu-item, .el-submenu__title li i {
  color: #999999;
}
.el-menu-item-group__title{display: none
}
