
div.consumeData {
  margin-right: 20px;
}
div.countInfo {
  padding: 20px 30px 20px 30px;
  background: #fff;
  margin-bottom: 20px;
}


/*div.countTitle {
  background: #f3f0f0;
  padding: 20px;
  color: #444;
}*/
div.countDetail {
  background: #fff;
}
span.labelAlign {
  float: left;
  margin-right: 10px;
  text-align: right;
  display: block;
  line-height: 35px;
  font-size: 14px;
  color: #555;
}
span.division {
  font-size: 14px;
  color: #555;
  width: 32px;
  display: inline-block;
  text-align: center;
}
span.timePeried {
  margin-left: 50px;
}
span.division {
  font-size: 14px;
  color: #555;
}
button.query {
  float: initial;
}
button.isClick {
  background: red;
}
div.timeSelectBtn {
  display: block;
  float: left;
}
div.timeSelectBtn button {
  margin-left: 8px;
  display: inline-block;
  border: 1px solid #ddd;
  outline: none;
  font-size: 12px;
  color: #666;
  /*background: rgba(66, 66, 66, 0.8);*/
  background: #fff;
  /*transition: all .2s linear 0s;*/
  box-sizing: border-box;
  height: 35px;
}
div.timeSelectBtn button:hover {
  border: 1px solid #bbb;
  color: #666;
}
div.timeSelectBtn button.active {
  /*background: rgb(66, 66, 66);*/
  background: rgba(	255,140,0, 0.8);
  color: #fff;
  border: 1px solid rgba(	255,140,0, 0.5);
}
.timeSelect_button {
  font-size: 12px;
  height: 35px;
}
.my_btn {
    width: 80px;
    float: right;
    height: 36px;
    line-height: 11px;
    color: #fff;
    /*margin-top: 10px;*/
    outline: none;
    border: none;
    /* border-radius: 4px; */
    background: rgba(52,52,67, 0.8);
}
.my_btn:hover {
    background: rgba(52,52,67, 0.9);
    color: #fff;
}
.el-input__inner {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  line-height: 1;
  outline: 0;
  padding: 3px 10px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}
.el-input__inner::-webkit-input-placeholder {
  color: #ddd;
}
.el-date-table td.current:not(.disabled), .el-date-table td.end-date, .el-date-table td.start-date {
  background: black !important;
  color: #fff !important;
}
.el-input__inner:hover {
  border: 1px solid #bbb;
}
.el-month-table td.current:not(.disabled) .cell {
  background: black !important;
  color: #fff;
}
.el-button:focus, .el-button:hover {
  color: #fff;
}



