
div.gmap{width:100%;height:100%;position: relative;
}
#map-container {
      width:100%;
      height:100%;
}
div.menu{position:absolute;width:30%;left:70%;top:0;bottom:0;right:0;z-index:3000;
}
div#myPageTop{
    padding:0 15px;
    height: 50px;
    width:80%;
    box-sizing:border-box;
    margin:0 auto;
    border-radius:5px;
    background: rgba(0, 0, 0, .3)
}
div#myPageTop div{height:50px;padding-top: 10px;box-sizing:border-box;
}
div#myPageTop i{
      height: 32px;
      line-height: 32px;
      margin-right:-1px;
      border: 1px solid rgba(0,0,0,.5); 
      display: inline-block;
      padding: 0 10px;
      border-bottom-left-radius: 5px;
      border-top-left-radius: 5px;
      width: 30px;
      overflow: hidden;
      box-sizing: border-box;
      vertical-align: middle;
      background:#fff;
      color:#000;
}
div#myPageTop input{line-height: 30px;
    height: 30px;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    text-indent: 5px;
    outline: none;
    border: 1px solid rgba(0,0,0,.5);
}
div.group-button{margin-top:20px;
}
div#panel{ 
            position: relative;
            background-color: white;
            max-height: 90%;
            overflow-y: auto;
           width:90%;
           margin:0 auto;
           top:10px;
}
div#editPath{margin-top: 10px;
}
div#editPath div.el-col-2 span{line-height: 32px
}
div.showCurrentCity{margin-top:10px;
}
div.amap-copyright{display: none!important;
}
.amap-logo img{display:none;
}



