
div.carUseDetail {
    background: #fff;
    margin: 0 auto;
    border: 1px solid #e7ecf1;
    width: 1000px;
}
div.carUseDetail table {
  border-collapse: collapse;
  width: 100%;
  padding: 0 30px 0 30px;
}
div.carUseDetail table tr td {
  /* border: 1px solid #f3f3f5; */
  padding: 5px 10px;
}
div.carUseDetail table tr td span.prex {
  display: inline-block;
  width: 80px;
  text-align: right;
  color: #bdb6b6;
  font-size: 14px;
  margin-right: 8px;
}
div.carUseDetail div.detailTitle h3 {
  line-height: 30px;
  /* width: 100%; */
  background: #555;
  color: #fff;
  margin-bottom: 20px;
  padding: 10px;
}
div.carUseDetail div.battery {
  float: right;
}
div.carUseDetail div.battery ul li {
  list-style-type: none;
  margin-bottom: 10px;
}
div.carUseDetail div.battery ul li span.online {
  text-align: center;
  display: block;
  width: 100px;
  padding: 5px 0px;
  background: green;
  color: #fff;
  font-size: 14px;
}
div.carUseDetail div.battery ul li span.lend {
  text-align: center;
  display: block;
  width: 100px;
  padding: 5px 0px;
  background: orange;
  color: #fff;
  font-size: 14px;
}
div.carUseDetail div.battery ul li span.capacity {
  text-align: center;
  display: block;
  width: 100px;
  padding: 5px 0px;
  background: #ff4949;
  color: #fff;
  font-size: 14px;
}
div.carUseDetail div.record {
  margin-top: 50px;
  padding: 10px;
}
div.carUseDetail div.recodeTable table {
  border-collapse: collapse;
  width: 100%;
}
div.carUseDetail div.recodeTable table thead tr th {
  font-weight: normal;
  text-align: left;
  border-bottom: 1px solid #afa7a7;
  padding: 5px 10px;
}
div.carUseDetail div.recodeTable table tbody tr td {
  border: none;
  border-bottom: 1px dotted #afa7a7;
  padding: 10px;
  color: #555;
}
div#carUseDetail_page {
  margin-top: 50px;
}
.el-tabs__active-bar {
  position: absolute;
  width: 0 !important;
  bottom: 0;
  left: 0;
  height: 3px;
  /* background-color: #20a0ff; */
  z-index: 1;
  transition: -webkit-transform .3s cubic-bezier(.645,.045,.355,1);
  transition: transform .3s cubic-bezier(.645,.045,.355,1);
  transition: transform .3s cubic-bezier(.645,.045,.355,1), -webkit-transform .3s cubic-bezier(.645,.045,.355,1);
  list-style: none;
}
.el-tabs__item.is-active {
  color: #444;
}
.el-tabs__nav-scroll {
    overflow: hidden;
    border-bottom: 2px solid #444;
}
.lang {
  width: 300px;
}
.help {
  height: 20px;
  width: 20px;
  line-height: 20px;
  cursor: help;
  display: inline-block;
  text-align: center;
  color: #666;
  border-radius: 50%;
  margin-left: 5px;
  border: 1px solid #666;
}




