
html,body,h1,h2,h3,h4,h5,h6,td,tr,th,table,thead,tbody,dl,dt,dd,p,span,ul,li,ol {
  	margin: 0;
  	padding: 0;
}
body {
  	font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}

  /*   abnormalData_header    */
#abnormalData_header {
    /* width: 100%; */
    height: 100px;
    padding: 10px 20px 10px 20px;
    margin-bottom: 20px;
    line-height: 60px;
    background: #fff;
    border: 1px solid #e7ecf1;
}
#abnormalData_header button {
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    float: right;
    background: #fff;
    border: 1px solid #c4c4c4;
    color: #1f2d3d;
    margin: 10px 20px;
    padding: 10px 15px;
    border-radius: 4px;
}
#abnormalData_header button:hover {
		background: rgba(106,215,209,0.8);
		color: #fff;
}
#abnormalData_header button.active {
		background: #ff9900;
		color: #fff;
}
#abnormalData_header div.abnormalData_checked {
    margin-left: 10px;
  	overflow: hidden;
    margin-top: -20px;
    height: 70px;
    width: 100%;
}
#abnormalData_header div.abnormalData_checked>span {
  	float: left;
  	color: #444;
  	font-weight: 500;
  	font-size: 14px;
}
#abnormalData_header div.abnormalData_checked .el-checkbox {
    width: 167px;
    height: 30px;
    display: block;
    float: left;
}
#abnormalData_header div.abnormalData_checked .el-checkbox:nth-of-type(1) {
 		margin-left: 10px;
}
#abnormalData_header div.abnormalData_checked .el-checkbox:nth-of-type(3) {
 		margin-right: 6px;
}
#abnormalData_header div.abnormalData_checked .el-checkbox:nth-of-type(5) {
 		margin-left: 10px;
}
#abnormalData_header .abnormalData_timeLine {
 		display: block;
 		width: 100%;
 		margin-left: 10px;
}
#abnormalData_header .abnormalData_timeLine span.demonstration {
 		float: left;
  	color: #444;
  	margin-right: 10px;
  	font-weight: 500;
  	font-size: 14px;
}
div.el-picker-panel {
    width: 220px;
    -webkit-transform-origin: center top 0px;
            transform-origin: center top 0px;
    z-index: 2001;
    position: fixed;
    top: 225px;
    left: 279px;
}
#abnormalData_header .abnormalData_timeLine button {
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #c4c4c4;
    color: #1f2d3d;
    margin: 10px 20px;
    padding: 10px 15px;
    border-radius: 4px;
}
#abnormalData_header .abnormalData_timeLine button:hover {
    color: #20a0ff;
    border-color: #20a0ff;
}


  /*  abnormalData_table  */
#abnormalData_table {
    padding: 30px 30px 10px 30px;
    background: #fff;
    border: 1px solid #e7ecf1;
    border-bottom: none;
}

	/*    abnormalData_page    */
#abnormalData_page {
		padding: 4px 10px 0 20px;
		padding-bottom: 88px;
		background: #fff;
		border: 1px solid #e7ecf1;
		border-top: none;
		min-height: 230px;
}




