
@media screen and (min-width:1367px) {
#addpartner_form {
    min-height: 30%;
    width: 45%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
    position: fixed;
    display: block;
    top: 54%;
    left: 53%;
    margin-left: -28%;
    margin-top: -27%;
    padding: 90px 80px 0px 50px;
    margin-right: 20px;
    border: 1px solid #ccc;
    background: #fff;
    border-radius: 2px;
}
}
@media screen and (max-width:1367px) {
#addpartner_form {
    height: 78%;
    width: 50%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
    position: fixed;
    display: block;
    overflow-y: scroll;
    top: 62%;
    left: 50%;
    margin-left: -34%;
    margin-top: -26.5%;
    padding: 70px 150px 0px 144px;
    margin-right: 20px;
    border: 1px solid #ccc;
    background: #fff;
    border-radius: 2px;
}
}
.addpartner_button:nth-of-type(1) {
  background: #f87e2b;
  border: none;
}
.addpartner_button:nth-of-type(1):hover {
  background: rgba(248, 126, 43, 0.9);
}
.addpartner_button:nth-of-type(2):hover {
  border: 1px solid rgb(248, 126, 43);
  color: rgb(248, 126, 43);
}
.addpartner_button {
  width: 120px;
  height: 50px;
}
#addpartner_title {
  position: absolute;
  left: 0px;
  top: 0px;
  padding-left: 20px;
  width: 98%;
  height: 60px;
  line-height: 60px;
  color: #444;
  font-size: 24px;
  border-bottom: 1px solid #eee;
  font-weight: 500;
  overflow: hidden;
}
#addpartner_title span {
  float: right;
  margin-right: 14px;
  cursor: pointer;
  overflow: hidden;
}
#addpartner_title span a {
  margin-right: 20px;
  font-size: 18px;
  overflow: hidden;
}
.el-input__inner {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  line-height: 1;
  outline: 0;
  padding: 3px 10px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}
.el-date-table td.current:not(.disabled), .el-date-table td.end-date, .el-date-table td.start-date {
  background: black !important;
  color: #fff !important;
}
.el-input__inner:hover {
  border: 1px solid #bbb;
}
.el-textarea__inner {
  display: block;
  resize: vertical;
  padding: 5px 7px;
  line-height: 1.5;
  width: 100%;
  color: #1f2d3d;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}



